<template>
    <div>
        <h4 class="page-header" id="aml01">{{serialNum1}}、客户反洗钱检查接口</h4>
        <h5 class="mt20">{{serialNum}}.1 服务地址</h5>
        <p class="bg-success">http://10.31.17.176:16604/ESB/CSBPS/amlService/amlPreReg</p>
        <p class="bg-success">备用地址: http://10.253.129.60:9002/csbps/serviceapp/services/rest/amlService/amlPreReg</p>
        <h5 class="mt20">{{serialNum}}.2 入参说明
            <a href="#submitReqParams">查看</a>
        </h5>
        <pre>同受理提交接口入参</pre>
        <h5 class="mt20">{{serialNum}}.3 出参说明</h5>
        <v-outParamTable :tableData="amlPreRegOutParams" :isList="true"></v-outParamTable>
        <h5 class="mt20 text-danger">出参报文示例(待完善的客户信息列表)</h5>
        <pre>
        [
            {"amlCheckStatus":"0","amlCustType":"02","birthDate":"1986-08-20 00:00:00","idNo":"110108198608226445","idType":"I","ipsnNo":"0","mtnApplyNo":"20180000000000022405","name":"谢搁","sex":"F"}
        ]</pre>

        <h4 class="page-header" id="aml02">{{serialNum2}}、反洗钱信息完善接口</h4>
        <h5 class="mt20">{{serialNum+1}}.1 服务地址</h5>
        <p class="bg-success">http://10.31.17.176:16604/ESB/CSBPS/amlService/procedurePersonAsyn</p>
        <p class="bg-success">备用地址: http://10.253.129.60:9002/csbps/serviceapp/services/rest/amlService/procedurePersonAsyn</p>
        <h5 class="mt20">{{serialNum+1}}.2 入参说明</h5>
        <v-inParamTable  :hasSlot="true">
            <tr>
                <td colspan="5" class="text-center bg-info">开始 +  mtnAmlPersonBO</td>
            </tr>
            <tr v-for="item in procedurePersonAsynReqParams">
                <td>{{item.chnName}}</td>
                <td>{{item.name}}</td>
                <td>{{item.required}}</td>
                <td>{{item.type}}</td>
                <td>{{item.remarks}}</td>
            </tr>
            <tr>
                <td colspan="5" class="text-center bg-info">结束 +  mtnAmlPersonBO</td>
            </tr>
        </v-inParamTable>
        <h5 class="mt20 text-danger">入参报文示例</h5>
        <pre>{mtnAmlPersonBO: {amlCheckStatus:"0", amlCustType:"02", amlRegStage:"02", birthDate:"1986-08-20", city:"北京市", cntyCode:"CHN", county:"东城区", custContact:"13666666666", homeAddress:"北京东城区30号", idNo:"110108198608226445", idType:"I", idValidDate:"2025-10-10", ipsnNo:"0", mtnApplyNo:"20180000000000022405", name:"谢搁", occClassCode:"12", occDtlCode:"02", occSubclsCode:"01", province:"北京市", regBranchNo:"000000", regClerkNo:"csbps", sex:"F", town:"", village:""}}</pre>
        <h5 class="mt20">{{serialNum+1}}.3 出参说明</h5>
        <v-outParamTable :tableData="[{chnName: '布尔值', name: 'boolean',  type: 'Boolean', remarks: 'true 成功，false失败'}]" :isList="false"></v-outParamTable>
        <h5 class="mt20 text-danger">出参报文示例</h5>
        <pre>{"success": true}</pre>
    </div>
</template>
<script>
    import InParamTable from '../common/InParamTable';
    import OutParamTable from '../common/OutParamTable';
    export default {
        components: {
            'v-inParamTable': InParamTable,
            'v-outParamTable': OutParamTable
        },
        props: {
            serialNum1: {
                type: String,
                default: ''
            },
            serialNum2: {
                type: String,
                default: ''
            },
            submitInParamAnchorId: {
                type: String,
                default: 'submitReqParams'
            }
        },
        data() {
            return {
                amlPreRegOutParams: [
                    {chnName: '客户类型', name: 'amlCustType', type: 'String', remarks: '01：投保人，02：被保人，03：受益人，00：其他'},
                    {chnName: '被保人序号', name: 'ipsnNo', type: 'String', remarks: ''},
                    {chnName: '姓名', name: 'name', type: 'String', remarks: ''},
                    {chnName: '证件类型', name: 'idType', type: 'String', remarks: ''},
                    {chnName: '证件号', name: 'idNo', type: 'String', remarks: ''},
                    {chnName: '证件有效期', name: 'idValidDate', type: 'String', remarks: ''},
                    {chnName: '出生日期', name: 'birthDate', type: 'String', remarks: ''},
                    {chnName: '性别', name: 'sex', type: 'String', remarks: ''},
                    {chnName: '职业大类', name: 'occClassCode', type: 'String', remarks: ''},
                    {chnName: '职业子类', name: 'occSubclsCode', type: 'String', remarks: ''},
                    {chnName: '职业细类', name: 'occDtlCode', type: 'String', remarks: ''},
                    {chnName: '国籍', name: 'cntyCode', type: 'String', remarks: ''},
                    {chnName: '省', name: 'province', type: 'String', remarks: ''},
                    {chnName: '市', name: 'city', type: 'String', remarks: ''},
                    {chnName: '县', name: 'county', type: 'String', remarks: ''},
                    {chnName: '镇', name: 'town', type: 'String', remarks: ''},
                    {chnName: '村', name: 'village', type: 'String', remarks: ''},
                    {chnName: '详细地址', name: 'homeAddress', type: 'String', remarks: ''},
                    {chnName: '电话', name: 'custContact', type: 'String', remarks: ''},
                    {chnName: '与权益人关系', name: 'relToBnfr', type: 'String', remarks: ''},
                    {chnName: '', name: 'amlRegStage', type: 'String', remarks: '忽略'},
                    {chnName: '', name: 'amlRegStat', type: 'String', remarks: '忽略'},
                    {chnName: '保全作业流水号', name: 'mtnNo', type: 'String', remarks: ''},
                    {chnName: '保全申请号', name: 'mtnApplyNo', type: 'String', remarks: ''},
                    {chnName: '保单号', name: 'cntrNo', type: 'String', remarks: ''},
                    {chnName: '操作员', name: 'regClerkNo', type: 'String', remarks: ''},
                    {chnName: '操作机构', name: 'regBranchNo', type: 'String', remarks: ''},
                    {chnName: '登记时间', name: 'regTime', type: 'String', remarks: ''},
                    {chnName: '检查标识', name: 'amlCheckStatus', type: 'String', remarks: '0-通过，1-不通过'},
                ],
                procedurePersonAsynReqParams: [
                    {chnName: '客户类型', name: 'amlCustType', required: 'N', type: 'String', remarks: '01：投保人，02：被保人，03：受益人，00：其他'},
                    {chnName: '被保人序号', name: 'ipsnNo', required: 'N', type: 'String', remarks: ''},
                    {chnName: '姓名', name: 'name', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '证件类型', name: 'idType', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '证件号', name: 'idNo', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '证件有效期', name: 'idValidDate', required: 'Y', type: 'Date', remarks: ''},
                    {chnName: '出生日期', name: 'birthDate', required: 'Y', type: 'Date', remarks: ''},
                    {chnName: '性别', name: 'sex', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '职业大类', name: 'occClassCode', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '职业子类', name: 'occSubclsCode', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '职业细类', name: 'occDtlCode', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '国籍', name: 'cntyCode', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '省', name: 'province', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '市', name: 'city', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '县', name: 'county', required: 'Y', type: 'String', remarks: ''},
                    {chnName: '镇', name: 'town', required: 'N', type: 'String', remarks: '空'},
                    {chnName: '村', name: 'village', required: 'N', type: 'String', remarks: '空'},
                    {chnName: '详细地址', name: 'homeAddress', required: 'Y', type: 'String', remarks: '四段地址, 镇和村的信息统一放入这里'},
                    {chnName: '电话', name: 'custContact', required: 'N', type: 'String', remarks: ''},
                    {chnName: '与权益人关系', name: 'relToBnfr', required: 'N', type: 'String', remarks: ''},
                    {chnName: '', name: 'amlRegStage', required: 'N', type: 'String', remarks: '忽略'},
                    {chnName: '', name: 'amlRegStat', required: 'N', type: 'String', remarks: '忽略'},
                    {chnName: '保全作业流水号', name: 'mtnNo', required: 'N', type: 'String', remarks: ''},
                    {chnName: '保全申请号', name: 'mtnApplyNo', required: 'N', type: 'String', remarks: ''},
                    {chnName: '保单号', name: 'cntrNo', required: 'N', type: 'String', remarks: ''},
                    {chnName: '操作员', name: 'regClerkNo', required: 'N', type: 'String', remarks: ''},
                    {chnName: '操作机构', name: 'regBranchNo', required: 'N', type: 'String', remarks: ''},
                    {chnName: '登记时间', name: 'regTime', required: 'N', type: 'Date', remarks: ''},
                    {chnName: '检查标识', name: 'amlCheckStatus', required: 'N', type: 'String', remarks: '0-通过，1-不通过'},
                ]
            }
        },
        computed: {
            serialNum() {
                switch(this.serialNum1) {
                    case '三':
                        return 3
                        break;
                    case '四':
                        return 4
                        break;
                    case '五':
                        return 5
                        break;
                    case '六':
                        return 6
                        break;
                        break;
                    case '七':
                        return 7
                        break;
                        break;
                    case '八':
                        return 8
                        break;
                        break;
                    case '九':
                        return 9
                        break;
                }
            }
        }
    }

</script>



